@import '~assets/styles/h3Imports';

.avatar {
  display: block;
  max-width: rem(49);
  position: relative;
  width: 100%;

  svg {
    height: auto;
    max-width: 100%;
    width: 100%;
  }

  &.small {
    background-color: var(--avatar-bg, #d9f1ff);
    border-radius: 180%;
    height: rem(32);
    width: rem(32);
  }

  &.medium {
    max-width: rem(49);
  }

  &.large {
    max-width: rem(80);
  }

  &.xlarge {
    max-width: rem(96);
  }
}

.image {
  border-radius: 180%;
}

.initials {
  color: var(--avatar-initial-colors, #323264);
  font-size: rem(19);
  font-weight: bold;
  left: 50%;
  line-height: line-height(24, 19);
  position: absolute;
  text-transform: uppercase;
  top: 50%;
  transform: translate(-50%, -50%);

  .small & {
    font-size: rem(14);
    line-height: line-height(20, 14);
  }

  .large &,
  .xlarge & {
    font-size: rem(27);
    line-height: line-height(32, 27);
  }
}
