@import '~assets/styles/h3Imports';

.menu-profile {
  align-items: center;
  background-color: var(--menu-profile-background);
  border-bottom: 1px solid var(--menu-profile-separator);
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 232px;
  overflow: hidden;
  padding: 34px 0;
  position: relative;
  text-align: center;
  width: 100%;

  .avatar-container {
    flex-grow: 1;
    min-width: 96px;

    figure {
      margin: 0;
    }
  }

  .user-info {
    display: flex;
    flex-direction: column;
  }

  .user-account-link {
    color: var(--menu-profile-link-color);
    font-size: rem(14);
    font-weight: 600;
    line-height: line-height(20, 14);
    min-height: 20px;
  }

  .username-heading {
    font-size: rem(21);
    font-style: normal;
    font-weight: bold;
    line-height: line-height(28, 21);
    margin: 4px 0;
    max-width: 100%;
    word-break: break-word;
  }

  .svg-left-top {
    left: 0%;
    pointer-events: none;
    position: absolute;
    top: 2px;
  }

  .svg-left-bottom {
    bottom: 0%;
    left: 0%;
    pointer-events: none;
    position: absolute;
  }

  .svg-right-top {
    pointer-events: none;
    position: absolute;
    right: 0%;
    top: 0%;
  }

  .svg-right-bottom {
    bottom: 0%;
    pointer-events: none;
    position: absolute;
    right: 0%;
  }
}
@media #{$large-down} {
  .svg-left-top,
  .svg-left-bottom,
  .svg-right-top,
  .svg-right-bottom {
    display: none;
  }

  .menu-profile {
    flex-direction: row;
    max-width: 504px;
    min-height: 0;
    padding: 24px;

    .avatar-container {
      flex-grow: 0;
      min-width: rem(56);

      figure {
        display: flex;
        max-width: rem(56);
      }
    }

    .user-info {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      height: 100%;
      justify-content: center;
      margin-left: rem(16);
      padding-bottom: 2px;
      text-align: left;

      h2 {
        margin-top: 0;
      }

      a {
        margin-right: auto;
      }
    }
  }
}
